@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Marck+Script&display=swap');
body {
  margin: 0px;
  font-family: 'Exo 2', sans-serif;
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
}
.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
.menu {
  display: flex;
  justify-content: space-around;
  padding: 0px;
  align-items: center;
}
.price {
  color: rgb(192, 170, 131);
  font-family: 'Noto Sans', sans-serif;
  padding-top: 16px;
  font-size: 30px;
}
.menu li {
  display: inline-block;
}
.menu li a {
  color: white;
  text-decoration: none;
  font-size: 20px;
}
header {
  background-color: rgb(112, 99, 76);
}
.menu li a:hover {
  color: rgb(0, 0, 0);
  transition: all 0.3s ease;
}
.li_menu_before::after {
  font-size: 20px;
  content: '>';
  margin: 0px 0 0 10px;
  color: rgb(255, 255, 255);
  position: absolute;
  transition: color 0.5s ease;
}
.li_menu_before::after {
  font-size: 20px;
  content: '>';
  margin: 0px 0 0 10px;
  color: rgb(255, 255, 255);
  position: absolute;
  transition: color 0.5s ease;
}
.li_menu_before:hover::after {
  font-size: 20px;
  content: 'Ë…';
  margin: 0px 0 0 10px;
  color: rgb(0, 0, 0);
  position: absolute;
  transition: color 0.5s ease;
}
.img_header {
  width: 100%;
  padding-top: 5px;
}
.search_header {
  width: 20px;
  padding-left: 40px;
}
.buy {
  font-size: 30px;
  padding: 5px;
  background-color: rgb(192, 170, 131);
  color: rgb(255, 255, 255);
  text-decoration: none;
}

.bgimg {
  display: grid;
  align-content: center;
  justify-content: center;
  background-image: url(../imgs/coffee.jpg);
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  border-bottom: 5px solid black;
}
.bgimg > div {
  z-index: 1;
}
.h1 {
  font-size: 10vh;
  color: rgb(0, 0, 0);
  font-family: 'Marck Script', cursive;
  text-align: center;
  margin: 20px;
}
.h1_ms {
  color: rgb(255, 255, 255);
}
.h2 {
  margin: 0px;
}
.h3 {
  font-family: 'Noto Sans', sans-serif;
  font-size: 30px;
  padding-bottom: 15px;
}
.bgimg::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(72, 50, 20, 0.761);
  opacity: 0.92;
}
.coffee_grid {
  display: grid;
  grid-template-columns: repeat(4, calc(25% - 8px));
  grid-template-rows: 1fr 1fr;
  grid-gap: 10px;
  margin-bottom: 20px;
  max-width: 1150px;
}
.coffee_items {
  background-color: white;
  text-align: center;
  align-content: center;
  color: rgb(0, 0, 0);
  border: 1px solid #f6f6f6;
}
.add_to_buy {
  padding: 15px 20px;
  text-transform: uppercase;
  font-family: 'Noto Sans', sans-serif;
  background-color: rgb(192, 170, 131);
  text-decoration: none;
  color: rgb(255, 255, 255);
  transition: all 0.3s ease;
}
.add_button {
  padding: 20px;
}
.coffee_items:hover {
  border: 0 0 100px rgba(0, 0, 0, 0.5);
}
.add_to_buy:hover {
  background-color: black;
  cursor: pointer;
}
.img_buy {
  width: 200px;
}
main {
  background-color: rgb(223, 223, 223);
}
.del {
  color: rgb(109, 109, 109);
  font-size: 20px;
  padding-top: 15px;
}
.image {
  position: relative;
  width: 100%;
}

.li_menu_before a {
  cursor: pointer;
}
.p {
  position: absolute;
  left: 15px;
  top: 0px;
}
.p span {
  padding: 5px 10px;
  color: white;
  background: rgb(218, 8, 8);
}
div div p span::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);

  height: 0;
  border-style: solid;
  border-width: 13px 0 13px 7px;
  border-color: transparent transparent transparent #fff;
  background-color: rgb(218, 8, 8);
}
.map_container {
  background-color: rgb(255, 255, 255);
}
.map_text {
  text-align: center;
  font-size: 35px;
  font-family: 'Noto Sans', sans-serif;
  padding-top: 25px;
  margin-top: 10px;
}
.map_text2 {
  font-size: 20x;
  font-family: 'Noto Sans', sans-serif;
  margin-top: 10px;
}
.map {
  width: 100%;
}
.map_wrap {
  margin-top: 85px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
}
.li_grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-column-gap: 40px;
  padding: 10px;
}
.li_grid li a {
  color: rgb(0, 0, 0);
  text-decoration: none;
}
.li_grid li a:hover {
  color: rgb(139, 84, 0);
  transition: all 0.3s ease;
}
.ul_child {
  display: flex;
  font-size: 30px;
  margin-top: 10px;
}
.li_grid li::before {
  content: '>';
  display: block;
  display: inline-block;
  font-size: inherit;
  position: absolute;
  margin: -1px 0 0 -20px;
}
.sponsors {
  margin: 0px;
  display: flex;
  justify-content: space-around;
  padding: 0px;
  align-items: center;
  background-color: rgb(223, 223, 223);
}
.sponsors_img {
  width: 100%;
  padding: 10px;
  margin-top: 30px;
  margin-bottom: 30px;
}
.testimonials {
