}
.inner {
  text-align: center;
  border: rgb(192, 170, 131) solid 8px;
  padding: 60px 65px 60px;
}
.mini {
  font-size: 15px;
  color: rgb(255 72 0);
}
.medium {
  font-size: 25px;
  color: rgb(255, 255, 255);
}
.icon_t {
  font-size: 20vh;
  color: rgb(255, 255, 255);
  margin-top: -120px;
}
.before_footer {
  padding: 20px;
  background-color: rgb(112, 99, 76);
  text-align: center;
}
.footer {
  background-image: url(../imgs/bg_footer.jpg);
  background-position: center;
  color: #ffffff;
  padding: 20px;
}
.content_2 {
  color: rgb(192, 170, 131);
  font-size: 35px;
}
.content_3 {
  color: rgb(192, 170, 131);
  font-size: 35px;
}

.content_4 {
  color: rgb(176 176 176);
}
.content_5 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}
.brown {
  color: rgb(192, 170, 131);
}
.content_5 div a {
  text-decoration: none;
  color: rgb(255, 255, 255);
  transition: color 0.5s ease;
  font-size: 17px;
}
.content_5 div a::before {
  content: '>';
  font-size: 16px;
  margin-right: 8px;
  color: rgb(255, 255, 255);
}
.content_5 div a:hover {
  color: rgb(192, 170, 131);
}
.container_wrap {
  padding: 15px;
  display: grid;
  align-items: center;
}
.container_wrap_item {
  padding: 15px;
  display: flex;
  align-items: center;
}
.btn_wrap {
  display: flex;
  justify-content: center;
}
.btn {
  cursor: pointer;
  text-align: center;
}
.flex_btn {
  display: flex;
  justify-content: center;
}
.btn a {
  background-color: rgb(192, 170, 131);
  padding: 15px;
  text-decoration: none;
  color: rgb(255, 255, 255);
  opacity: 1;
  transition: all 0.3s ease 0s;
}
.btn a:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
  box-shadow: 0px 0px 1px 3px black;
}
.text_logo {
  font-family: 'Marck Script', cursive;
  font-size: 130px;
  color: rgb(255 255 255);
}

/* formA!!! */

.transparent {
  position: relative;
  max-width: 600px;
  padding: 40px;
  margin: 50px auto;
  background-image: url(../imgs/coffe_bg.jpg);
  background-size: cover;
  border-radius: 20px;
}
.form_inner {
  position: relative;
}
.form_inner h3 {
  position: relative;
  margin-top: 0;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 26px;
  text-transform: uppercase;
}
.form_inner h3:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -6px;
  height: 2px;
  width: 60px;
  background: #1762ee;
}
.form_inner label {
  display: block;
  padding-left: 15px;
  font-family: 'Roboto', sans-serif;
  color: rgb(23 98 238);
  text-transform: uppercase;
  font-size: 14px;
}
.form_inner input {
  display: block;
  width: 100%;
  margin: 10px 0 15px;
  border-width: 0;
  line-height: 40px;
  border-radius: 20px;
  outline: none;
  color: rgb(0, 0, 0);
  font-family: 'Roboto', sans-serif;
}
.form_inner input[type='checkbox'] {
  position: absolute;
  opacity: 0;
}
#custom_checkbox + label {
  position: relative;
  margin: 20px 0;
  text-transform: none;
  cursor: pointer;
}
#custom_checkbox + label:before {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  vertical-align: text-top;
  background: rgb(255, 255, 255);
}
#custom_checkbox:checked + label:before {
  background: #1762ee;
}
#custom_checkbox:checked + label:after {
  content: '';
  position: absolute;
  width: 2px;
  height: 2px;
  left: 20px;
  top: 9px;
  background: white;
  box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white,
    4px -6px 0 white, 4px -8px 0 white;
  transform: rotate(45deg);
}

.form_wrap {
  padding: 150px;
}
.container_form {
  background-color: rgb(192 170 131);
}
.btn_submit:hover {
  background-color: #0647c1;
  cursor: pointer;
  transition: 0.3s;
}
.btn_submit {
  background: #1762ee;
}
.text_area {
  border-radius: 10px;
}

.products_wrap {
  display: flex;
  justify-content: center;
  padding: 200px;
}
.bg_prod {
  background-color: rgb(223, 223, 223);
}
.sponcor_descr {
  font-size: 50px;
  display: flex;
  justify-content: center;
  font-family: 'Marck Script', cursive;
  background-color: white;
}
.coffee_item {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
  justify-content: center;
  align-items: center;
  display: flex;
}
.video_frame {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
